import React, { useEffect } from "react";

import "./shareSNS.css";
import kakao from "../../assets/kakaoshare.png";

import link from "../../assets/linkshare.png";

const ShareSNS = () => {
  const url = window.location.href; // url
  useEffect(() => {
    initKakao(); //
  }, []);

  //  init
  const initKakao = () => {
    if (window.Kakao) {
      const kakao = window.Kakao;
      if (!kakao.isInitialized()) {
        kakao.init("13ce2c4185e1fe0707553109dcf25bb2");
        // kakao.init("process.env.REACT_APP_KAKAO_TOKEN");
      }
    }
  };

  //   
  const shareKakao = () => {
    //       sendDefault  .
    window.Kakao.Link.sendDefault({
      objectType: "feed",
      content: {
        title: "  ",
        description: "# # # # # #",
        imageUrl:
          "http://mud-kage.kakao.co.kr/dn/Q2iNx/btqgeRgV54P/VLdBs9cvyn8BJXB3o7N8UK/kakaolink40_original.png",
        link: {
          mobileWebUrl: url,
          webUrl: url,
        },
      },
      social: {
        likeCount: 286,
        commentCount: 45,
        sharedCount: 845,
      },
      buttons: [
        {
          title: " ",
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        {
          title: " ",
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
      ],
    });
  };

  const doCopy = (text) => {
    //  1.
    if (!document.queryCommandSupported("copy")) {
      return alert("   .");
    }
    //  2.
    const textarea = document.createElement("textarea");
    textarea.value = text;
    textarea.style.top = 0;
    textarea.style.left = 0;
    textarea.style.position = "fixed";
    //  3.
    document.body.appendChild(textarea);
    // focus() ->   
    textarea.focus();
    // select() ->       
    textarea.select();
    //  4.
    document.execCommand("copy");
    //  5.
    document.body.removeChild(textarea);
    alert(" .");
  };
  return (
    <div className="sharesnscomponent">
      <div className="kakaoandclipboard">
        <div className="kakao" id="test_kakao">
          <img
            className="shareIcon"
            src={kakao}
            alt="kakaotalk"
            width="65px"
            height="65px"
            onClick={shareKakao}
          />
        </div>
        <div className="clipboard" id="test_link">
          <img src={link} onClick={() => doCopy("!")} />
        </div>
      </div>
    </div>
  );
};

export default ShareSNS;
